import axios from 'axios'

let localhost = 'http://www.jummy.top';
// let localhost = 'http://127.0.0.1:8000';

export const articleList = function (category) {
  if(category) {
    return axios.get(localhost.toString() + '/api/aricle/?' + 'category=' + category)
  }
  else {
    return axios.get(localhost.toString() + '/api/aricle/')
  }
};

export const articleDetail = function (id) {
  return axios.get(localhost.toString() + '/api/aricle/' + id + '/');
};

export const categoryList = () => {
  return axios.get(localhost+ '/api/category/')
};

export const gitAuth = (code) => {
  return axios.get(localhost + '/users/git/oauth/?code=' + code);
};

export const home_img = () => {
  return axios.get(localhost + '/home_img/');
};

export const page_data = (url) => {
  return axios.get(url);
};



