<template>
  <div id="detail">
    <h3>{{ articleDetail.title }}</h3>
    <div class="icon-post">
      <span class="iconfont">&#xe64d; {{ articleDetail.add_time }} </span>
      <span class="iconfont">&#xe639; {{ articleDetail.pv }} </span>
      <span class="iconfont">&#xe621; {{ category_name }} </span>
      <span class="iconfont">&#xe643; 1</span>
    </div>
    <div id="content" v-html="articleDetail.html" v-highlight>
    </div>
    <transition name="fade">
      <!--<Comment @showDowaHome="showDowaHome"></Comment>-->
    </transition>
  </div>
</template>

<script>
  import {articleDetail} from "../../api/api";
  import Comment from './Comment'

  export default {
    name: "Detail",
    props: ['id'],
    data(){
      return {
        detail_id: '',
        articleDetail: {},
        category_name: ''
      }
    },
    components: {
      Comment,
    },
    methods: {
      showDowaHome() {
        this.$emit('showDowa');
        console.log(this.$emit('showDowa'));
        return
      },
      initDetailData(response) {
        this.articleDetail = response.data;
        this.category_name = this.articleDetail.category.name;
      },
    },
    mounted() {
      this.detail_id = this.id;
      articleDetail(this.detail_id).then(this.initDetailData);
      // this.$nextTick(function () {
      //   PR.prettyPrint();
      //   console.log('111')
      // })
    }
  }
</script>

<style lang="css" scoped>
  /*@import "../../../node_modules/code-prettify/src/prettify.css";*/
  #icon span {
    font-size: 4em;
    margin-left: 75%;
    color: #A6A6A6;
  }

  #icon span:hover {
    color: black;
  }

  #icon p {
    display: inline-block;
    font-size: 20px;
    color: #A6A6A6;
  }

  #detail {
    word-break: break-word;
    margin: 10% 17% 10%;
    /*border: black solid 1px;*/
    border-radius: 10px;
    box-shadow: 1px 1px 13px #888888;
    padding-bottom: 100px;
    padding-top: 50px;
    overflow: hidden;
    background-color: #ffffff;
  }

  h3 {
    text-align: center;
    font-size: 2em;
  }

  #content {
    font-size: 20px;
    margin: 5px 60px 100px;
    letter-spacing: 2px;
    line-height: 50px;
    overflow: hidden;
  }

  .icon-post {
    margin-top: 0px;
    text-align: center;

  }

  .icon-post span {
    font-size: 1em;
    color: #696969;
    margin-right: 20px;
  }

  .icon-post span:hover {
    color: black;
  }

  .icon-post p {
    display: inline-block;
    color: #696969;
    line-height: 1px;
    vertical-align: text-bottom;
    margin-right: 10px;
  }

  .user_like img {
    width: 45px;
    border-radius: 50px;
    border: black solid 1px;
  }

  .user_like span {
    text-align: center;
    display: block;
    font-size: 12px;
  }

  @media only screen and (min-width: 4000px) and (max-width: 8000px) {
    .icon-post span {
      font-size: 70px;
    }

    .icon-post span:hover {
      color: black;
    }

    .icon-post p {
      display: inline-block;
      color: #696969;
      line-height: 50px;
    }

    h3 {
      font-size: 7em;
    }

    #content {
      font-size: 60px;
      margin: 20px 200px 200px;
      letter-spacing: 2px;
      line-height: 150px;
    }

    #icon span {
      font-size: 15em;
      margin-left: 75%
    }

    #icon p {
      display: inline-block;
      font-size: 6em;
    }

    .user_like img {
      width: 150px;
      border-radius: 100px;
      border: #696969 solid 6px;
    }

    .user_like span {
      display: block;
      font-size: 39px;
    }
  }

  @media only screen and (min-width: 200px) and (max-width: 500px) {
    #detail {
      margin: 15% 5% 5%;
      border: #A6A6A6 solid 1px;
      padding-top: 0px
    }

    .icon-post span {
      font-size: 15px;
      line-height: 0px;
      margin-right: 0px;
    }

    .icon-post p {
      display: inline-block;
      color: #696969;
      line-height: 0px;
      margin-right: 0px;
    }

    h3 {
      font-size: 1.8rem;
    }

    #content {
      font-size: 15px;
      margin: 2px 20px 20px;
      letter-spacing: 2px;
      line-height: 25px;
    }

    #icon span {
      font-size: 1em;
      margin-left: 75%
    }

    #icon p {
      display: inline-block;
      font-size: 0.4em;

    }

    .user_like img {
      width: 20px;
      border-radius: 100px;
      border: #696969 solid 1px;
    }

    .user_like span {
      display: block;
      font-size: 3.9px;
    }
  }

  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
  }

  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */
  {
    opacity: 0;
  }
</style>



