<template>
  <div id="comment">
    <h2>留言板</h2>
    <span></span>
    <textarea v-model="content" maxlength="50">
    </textarea>
    <button id="hom_btn">
      确定
    </button>

    <ul>
      <li class="border_li">
        <div class="comment_list">
          <div class="userinfo">
            <a><img class="img blockis"></a>
            <span class="blockis blockis_s">评论者</span>
          </div>
          <div class="leftd" :id="'comment_1'+item.id">
          <span ng-class="leftd_h">
              <img ng-src="./img/c_pic.pn"/>
          </span>
            <div class="speech left" ng-class="speech left">
              评论内容
            </div>
          </div>
          <div class="icon_comment">
            <span class="iconfont">&#xe64d; 时间</span>
            <span class="iconfont" @click="CommentLike(item.id,index,-1,1)"
                  v-if="item.is_like">&#xe627; 点赞</span>
            <span class="iconfont" @click="CommentLike(item.id,index,-1,2)"
                  v-else>&#xe643; {{item.like_num}}</span>
            <span class="iconfont" @click="showText('text_'+item.id)">&#xe63e; 回复</span>
          </div>
          <div class="comment_text comment_btn" :id="'text_'+item.id">
            <textarea v-model="content_sub" class="text"></textarea>
            <div>
              <button
                @click="createComment({object_id:detail_pk,parent_id:item.id,root_id:item.id,reply_to_id:item.user_id}, index, 2)">
                确定
              </button>
              <button @click="closText">
                取消
              </button>
            </div>
          </div>
          <div class="comment_list margin sub_comment"
               v-for="(sub_item,sub_index) in item.root"
               :key="sub_item.id">
            <div class="userinfo_sub">
              <a :href="sub_item.git_path"><img class="img blockis" :src="sub_item.user_img" alt=""></a>
              <span class="blockis blockis_s">{{sub_item.username}}</span>
            </div>
            <div class="leftd">
            <span ng-class="leftd_h">
                <img ng-src="./img/c_pic.pn"/>
            </span>
              <div class="speech left" ng-class="speech left">
                回复@{{sub_item.reply_to}}：{{sub_item.content}}
              </div>
            </div>
            <div class="icon_comment_sub">
              <span class="iconfont">&#xe64d; {{sub_item.time}}</span>
              <span class="iconfont"
                    @click="CommentLike(sub_item.id,index,sub_index,1)"
                    v-if="sub_item.is_like">&#xe627; {{sub_item.like_num}}</span>
              <span class="iconfont"
                    @click="CommentLike(sub_item.id,index,sub_index,2)"
                    v-else>&#xe643; {{sub_item.like_num}}</span>
              <span class="iconfont"
                    @click="showText('rep_text_'+sub_item.id)">&#xe63e; 回复</span>
            </div>
            <div class="comment_text sub_comment_btn"
                 :id="'rep_text_'+sub_item.id">
              <textarea v-model="content_sub" class="text"></textarea>
              <div>
                <button
                  @click="createComment({object_id:detail_pk,parent_id:sub_item.id,root_id:item.id,reply_to_id:sub_item.user_id}, index, 2)">
                  确定
                </button>
                <button @click="closText">
                  取消
                </button>
              </div>
            </div>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: "Comment"
  }
</script>

<style scoped>
  .sub_comment_btn div {
    margin-left: 70%;
  }

  .comment_btn div {
    margin-left: 73%;
  }

  .sub_comment {
    border-top: dashed 1px;
    padding-top: 25px;
  }

  .load_hover:hover {
    transition: 0.5s;
    transform: scale(1.1, 1.1);
  }

  #hom_btn {
    margin-left: 90%;
    line-height: 40px;
    width: 80px;
    border-radius: 10px;
    border: black solid 1px;
    background: #ffff;
    font-size: 15px;
    color: black;
  }

  .comment_text div button {
    line-height: 40px;
    width: 80px;
    border-radius: 10px;
    border: black solid 1px;
    background: #ffff;
    font-size: 15px;
    color: black;
    display: inline-block;
    margin-right: 30px;
  }

  #comment {
    margin: 40px 6% 100px;
  }

  .img {
    margin-left: 15%;
    height: 50px;
    width: 50px;
    border-radius: 50px;
    border: solid black 1px;
  }

  .comment_text {
    display: none;
  }

  .comment_text button {
    display: inline-block;
  }

  h2 {
    color: black;
    border-bottom: #696969 solid 3px;
    padding-bottom: 15px;
    margin-bottom: 20px;
  }

  span:hover {
    color: black;
  }

  textarea {
    width: 100%;
    height: 200px;
    resize: none;
    font-size: 1.8em;
    margin-bottom: 20px;
    border-radius: 8px;
    border: solid 2px black;
  }

  .text {
    width: 95%;
    height: 200px;
    margin-top: 10px;
    margin-bottom: 5px;
    border-radius: 8px;
    border: solid 2px black;
  }

  .comment_list {
    margin-top: 10px;
    margin-bottom: 10px;
    position: relative;
  }

  ul {
    padding: 0;
  }

  ul li {
    list-style-type: none;
    border-bottom: #a59999 solid 1px;
    overflow: hidden;
  }

  .userinfo {
    display: inline-block;
    position: absolute;
    width: 11%
  }

  .userinfo_sub {
    display: inline-block;
    position: absolute;
    width: 10%
  }

  .userinfo_sub span {
    margin-top: 10px;
    font-size: 10px;
  }

  .userinfo span {
    /*margin-top: 10px;*/
    font-size: 10px;
  }

  .blockis {
    display: block;
    color: black;
    text-align: center;
  }

  blockis_s {
    width: 100%;
    font-size: 4px;
  }

  .userinfo a img {
    margin-bottom: 10px;
  }

  .content {
    display: inline-block;
    width: 80%;
    margin-top: 0;
  }

  .content div {
    overflow: hidden;
  }

  .message {
    margin-bottom: 20px;
  }

  span {
    display: inline-block;
    margin-right: 15px;
  }

  .margin {
    margin-left: 8%;
  }

  .icon_comment {
    color: #696969;
    margin-left: 10%;
  }

  .icon_comment span {
    font-size: 12px;
    color: black;
  }

  .icon_comment_sub {
    color: #696969;
    margin-left: 10%;
  }

  .icon_comment_sub span {
    font-size: 12px;
    color: black;
  }

  .load_none {
    margin-left: 40%;
    font-size: 1.5em;
    margin-top: 50px;
    color: #696969;
  }

  div.speech {
    margin: 10px 0;
    padding: 8px;
    table-layout: fixed;
    word-break: break-all;
    position: relative;
    background: -webkit-gradient(linear, 50% 0%, 50% 100%, from(#ffffff), color-stop(0.1, #ececec), color-stop(0.5, #dbdbdb), color-stop(0.9, #dcdcdc), to(#8c8c8c));
    border: 1px solid #989898;
    border-radius: 8px;
  }

  div.speech:before {
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    left: 15px;
    top: -20px;
    border: 10px solid;
    border-color: transparent transparent #989898 transparent;
  }

  div.speech:after {
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    left: 17px;
    top: -16px;
    border: 8px solid;
    border-color: transparent transparent #ffffff transparent;
  }

  div .left {
    display: inline-block;
    box-shadow: 2px 2px 2px #CCCCCC;
    margin-left: 10px;
    max-width: 75%;
    position: relative;
    background: -webkit-gradient(linear, 50% 0%, 50% 100%, from(#ffffff), color-stop(0.1, #eae8e8), color-stop(0.4, #E3E3E3), color-stop(0.8, #DFDFDF), to(#D9D9D9));
  }

  div .left:before {
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    top: 9px;
    bottom: auto;
    left: -10px;
    border-width: 9px 10px 9px 0;
    border-color: transparent #989898;
  }

  div .left:after {
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    top: 10px;
    bottom: auto;
    left: -8px;
    border-width: 8px 9px 8px 0;
    border-color: transparent #eae8e8;
  }

  .leftimg {
    /*float: left;*/
    /*margin-top: 10px;*/
  }

  .leftd {
    margin-left: 7%;
    display: inline-block;
    width: 85%;
  }

  .leftd_h {
    width: 39px;
    height: 39px;
    border-radius: 100%;
    display: block;
    overflow: hidden;
  }

  .leftd_h img {
    display: block;
    width: 100%;
    height: auto;
  }

  div .left:before {
    border-width: 3px 9px 3px 0;
  }

  div .left:after {
    border-width: 3px 9px 3px 0;
  }

  @media only screen and (min-width: 3000px) and (max-width: 4000px) {
    button {
      line-height: 120px;
      width: 320px;
      border: #696969 solid 3px;
      border-radius: 30px;
    }

    textarea {
      height: 600px;
    }

    .img {
      height: 162px;
    }

    .comment_list[data-v-9a4cccc2] {
      margin-top: 10px;
      margin-bottom: 40px;
    }

    .text {
      height: 600px;
    }

    .icon_comment span {
      font-size: 40px;
    }

    .message {
      font-size: 3em;
    }
  }

  @media only screen and (min-width: 4000px) and (max-width: 8000px) {
    button {
      line-height: 150px;
      width: 400px;
      border: #696969 solid 3px;
      border-radius: 30px;
    }

    textarea {
      height: 600px;
    }

    .img {
      height: 162px;
    }

    .comment_list {
      margin-top: 10px;
      margin-bottom: 40px;
    }

    .text {
      height: 600px;
    }

    .icon_comment span {
      font-size: 40px;
    }

    .message {
      font-size: 3em;
    }
  }

  @media only screen and (max-width: 900px) {

  }

  @media only screen and (min-width: 200px) and (max-width: 500px) {
    #hom_btn {
      margin-left: 70%;
      line-height: 30px;
    }

    .icon_comment {
      margin-left: 18%;
    }

    .userinfo {
      width: 12%;
    }

    .userinfo_sub {
      width: 13%;
    }

    .img {
      height: 28px;
      width: 28px;
    }

    .blockis {
      font-size: 7px;
      margin-right: 0;
      overflow: hidden;
    }

    .icon_comment span {
      font-size: 8px;
    }

    .icon_comment_sub span {
      font-size: 8px;
    }

    div .left {
      max-width: 65%;
    }


    .leftd {
      margin-left: 15%;
      width: 60%;
    }

    .comment_text div button {
      line-height: 20px;
      width: 50px;
      border-radius: 5px;
      border: black solid 1px;
      background: #ffff;
      font-size: 8px;
      color: black;
      display: inline-block;
      margin-right: 10px;
    }

    .sub_comment_btn div {
      margin-left: 50%;
    }

    .comment_btn div {
      margin-left: 53%;
    }

    .load_none {
      margin-left: 40%;
      font-size: 0.7em;
      margin-top: 40px;
    }

    span {
      margin-right: 6px;
    }

    .userinfo {
      width: 15%;
    }

    .userinfo_sub {
      width: 15%;
    }

    .icon_comment_sub {
      color: #696969;
      margin-left: 19%;
    }

    div.speech {
      font-size: 12px;
    }

    div .left:before {
      top: 7px;
      left: -10px;
      border-width: 3px 9px 3px 0;
    }

    div .left:after {
      top: 8px;
      left: -9px;
      border-width: 3px 9px 3px 0;
    }
  }
</style>



